import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAlert } from "../../../redux/slices/uiSlice";
import { ReactComponent as Success } from "../../../svg/success.svg";
import { ReactComponent as Error } from "../../../svg/error.svg";

export default function Alert() {
  const { alert } = useSelector((state) => state.userInterface);
  const dispatch = useDispatch();
  let setType = alert?.success
    ? ["green_alert", <Success />, alert?.message]
    : ["red_alert", <Error />, alert?.message];

  useEffect(() => {
    let timeout = setTimeout(() => {
      dispatch(setAlert(null));
    }, 2500);
    return () => clearTimeout(timeout);
  }, [alert]);

  return (
    <div
      id="Alert"
      className={`${!alert ? "hide" : ""} ${setType[0]} background_component`}
    >
      {setType[1]}
      <span>{setType[2]}</span>
    </div>
  );
}
