import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, Outlet, Navigate } from "react-router-dom";
import { getAuthData } from "../../../redux/slices/authSlice";
import {
  connectToSocket,
  disconnectFromSocket,
} from "../../../redux/middlewares/socketConnection";
import useSocketEventHandlers from "../../../hooks/socketHooks/socketOnEvent";

export default function RequireAuth() {
  const { socket } = useSelector((state) => state.webSocket);
  const { isAuthenticated } = useSelector((state) => state.userAuth);
  const auth = useSelector(getAuthData);
  const dispatch = useDispatch();
  const location = useLocation();
  useSocketEventHandlers(socket);

  useEffect(() => {
    if (isAuthenticated && !socket) {
      dispatch(connectToSocket(auth?.socketToken));
    }

    return () => {
      if (!isAuthenticated && socket) {
        dispatch(disconnectFromSocket());
      }
    };
  }, [socket]);

  return isAuthenticated ? (
    <Outlet />
  ) : (
    <Navigate to="/signin" state={{ from: location }} replace />
  );
}
