export default function shortName(text = "", space = 2, textLen = 10) {
  let words = text.trim().split(" ");
  let textLine = text.trim();
  if (words.length > space) {
    return words.slice(0, space).join(" ") + "...";
  } else if (textLine.length > textLen) {
    return textLine.slice(0, textLen) + "...";
  }
  return text;
}
