import "./navbar.scss";
import {
  setShowSettings,
  setAccountSettings,
} from "../../../../redux/slices/uiSlice";
import { useDispatch, useSelector } from "react-redux";
import useLogout from "../../../../hooks/httpHooks/useLogoutHook";
import { getUserData } from "../../../../redux/slices/authSlice";

export default function NavDropdown() {
  const LOGOUT = useLogout();
  const dispatch = useDispatch();
  const { avatar } = useSelector(getUserData);
  const {
    authData: { user },
  } = useSelector((state) => state.userAuth);
  const { showSettings, showAccountSettings } = useSelector(
    (state) => state.userInterface
  );

  const handleClick = (e) => {
    e.stopPropagation();
    let { id } = e.target;
    if (id === "Manage_Profile") {
      dispatch(setAccountSettings(!showAccountSettings));
    } else if (id === "Settings") {
      dispatch(setShowSettings(!showSettings));
    }
  };

  return (
    <div className="nav-dropdown fadeInOut-anim background_component">
      <ul className="nav-dropdown-menu">
        <li className="current-user">
          <img src={avatar} alt="user_avatar" />
          <br />
          <span>{user.username}</span>
          <br />
          <span>{user.email}</span>
        </li>
        <li className="hoverIt">
          <button className="hoverIt" id="Manage_Profile" onClick={handleClick}>
            <i className="fas fa-user-circle"></i>Manage Account
          </button>
        </li>

        <li className="hoverIt">
          <button id="Settings" className="hoverIt" onClick={handleClick}>
            <i className="fas fa-cog"></i>Settings
          </button>
        </li>
        <li className="hoverIt">
          <button onClick={LOGOUT} type="button">
            <i className="fas fa-sign-out-alt"></i>Logout
          </button>
        </li>
      </ul>
    </div>
  );
}
