import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  dropdown: false,
  showSettings: false,
  showAccountSettings: false,
  isLoading: false,
  alert: null,
  isVerified: true,
  showNoteDropdown: false,
};

const uiSlice = createSlice({
  name: "UserInterface",
  initialState,
  reducers: {
    setDropdown: (state, action) => {
      state.dropdown = action.payload;
    },
    setNoteDropdown: (state, action) => {
      state.showNoteDropdown = action.payload;
    },
    setAppFocus: (state) => {
      state.dropdown = false;
    },
    setShowSettings: (state, action) => {
      state.showSettings = action.payload;
      state.dropdown = false;
    },
    setAccountSettings: (state, action) => {
      state.showAccountSettings = action.payload;
      state.dropdown = false;
    },
    setAppLoading: (state, action) => {
      state.isLoading = action.payload;
    },
    setAlert: (state, action) => {
      if (action.payload === null) {
        state.alert = null;
      } else {
        state.alert = action.payload;
      }
    },
    setAppLoadingAndAlert: (state, action) => {
      // To set alerts and app loading at once
      state.isLoading = action.payload.isLoading;
      if (action.payload.alert === null) {
        state.alert = null;
      } else {
        state.alert = action.payload.alert;
      }
    },
    setVerified: (state, action) => {
      state.isVerified = action.payload;
    },
  },
});

export const {
  setDropdown,
  setNoteDropdown,
  setAppFocus,
  setShowSettings,
  setAccountSettings,
  setAppLoading,
  setAlert,
  setAppLoadingAndAlert,
  setVerified,
} = uiSlice.actions;
export default uiSlice.reducer;
