import { useState } from "react";
import useRecover from "../../../hooks/componentHooks/recoverHook";
import "./recover.scss";
import ERROR from "../../reusableComponents/infomativeComponents/ERROR";
import Ellipsis from "../../reusableComponents/loadingComponents/ellipsis";
import EmailSuccess from "./emailSuccess";

export default function Recover() {
  const SUBMIT_INFO = useRecover();
  let [getValue, setValue] = useState({
    email: "",
    error: null,
    isLoaded: true,
    success: false,
  });

  async function submitData(e) {
    e.preventDefault();
    setValue((rest) => ({ ...rest, isLoaded: false }));
    const { message, status } = await SUBMIT_INFO("/user/password/recovery", {
      email: getValue.email,
    });

    status !== 200
      ? setValue((rest) => ({
          ...rest,
          isLoaded: true,
          success: false,
          error: { message, status },
        }))
      : setValue((rest) => ({
          ...rest,
          error: null,
          success: true,
          isLoaded: true,
        }));
  }

  function handleChange(e) {
    const { value, name } = e.target;
    setValue((rest) => ({ ...rest, [name]: value, error: null }));
  }

  return (
    <div className="recover-container">
      {!getValue.success ? (
        <div className="reset">
          <div className="resetPassword">
            <img
              src="https://die739ygwougn.cloudfront.net/assets/logo192.png"
              alt="app_logo"
            />
            <h1>Reset password</h1>
            <p>Enter your registered email below to reset your password.</p>
          </div>

          <form onSubmit={submitData}>
            <div>
              <input
                type="email"
                name="email"
                value={getValue.email}
                onChange={handleChange}
                placeholder="Email"
                required
              />
              <ERROR error={getValue.error} />
            </div>

            <div id="submitEmail">
              {!getValue.isLoaded ? (
                <Ellipsis />
              ) : (
                <input type="submit" value="Submit" className="myBtn" />
              )}
            </div>
          </form>
        </div>
      ) : (
        <EmailSuccess email={getValue.email} />
      )}
    </div>
  );
}
