import { appearance } from "../../../../redux/slices/authSlice";
import { ReactComponent as NoNotes } from "../../../../svg/no-notes.svg";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import dateFormat from "../../../../modules/dateFormat";
import textLength from "../../../../modules/textLength";
import useGetNotes from "../../../../hooks/componentHooks/getNotes";

export default function SavedNotes() {
  const { grid_view } = useSelector(appearance);
  const { isLoading } = useSelector((state) => state.userInterface);
  const notes = useGetNotes();
  let isGrid = grid_view ? ["notes-grid", 20, 40] : ["notes-list", 10, 10];

  const setParam = (param) => param?.noteID;
  let isNotesAvailable =
    isLoading && !notes.length
      ? "Getting Notes..."
      : "Didn't find any saved note. Click the + button to create a new note.";

  return (
    <div className="my_notes">
      {!isLoading && notes.length ? (
        <ul className={isGrid[0]}>
          {notes.map((e, i) => (
            <li className="notes_item" key={i}>
              <Link
                style={{ borderColor: e.colorCode }}
                className="saved_note hoverIt"
                to={`/note/${setParam(e)}`}
              >
                <div className="title_content">
                  <h3 className="note_title">
                    {textLength(e.noteTitle, 3, 20)}
                  </h3>
                  <p className="note_content">
                    {textLength(e.noteContent, isGrid[1], isGrid[2])}
                  </p>
                </div>
                <div className="color_time">
                  <span className="time">{dateFormat(e.timestamp)} </span>
                  <span
                    className="colorCode"
                    style={{ background: e.colorCode }}
                  ></span>
                </div>
              </Link>
            </li>
          ))}
        </ul>
      ) : (
        <div className="mySvg my_notes">
          <NoNotes />
          <p>{isNotesAvailable}</p>
        </div>
      )}
    </div>
  );
}
