import "./manageAccount.scss";
import Header from "../../reusableComponents/header";
import { setAccountSettings } from "../../../redux/slices/uiSlice";
import DangerZone from "./elements/dangerZone";
import PersonalInfo from "./elements/accountInfo";
import UserAvatar from "./elements/userAvatar";

export default function ManageAccount() {
  return (
    <div id="manage_account">
      <div className="window background_component fadeIn">
        <Header name={"Manage Account"} func={setAccountSettings} />
        <UserAvatar />
        <PersonalInfo />
        <DangerZone />
      </div>
    </div>
  );
}
