import React from "react";
import { useSelector } from "react-redux";
import { Outlet } from "react-router-dom";
import Alert from "./reusableComponents/infomativeComponents/Alert";
import Ellipsis from "./reusableComponents/loadingComponents/ellipsis";

export default function Layout() {
  const { isLoading } = useSelector((state) => state.userInterface);
  return (
    <main>
      <Outlet />
      <div id="alert_container">{!isLoading ? <Alert /> : <Ellipsis />}</div>
    </main>
  );
}
