import { useDispatch } from "react-redux";
import { setAppLoadingAndAlert } from "../../redux/slices/uiSlice";
import usePatchHook from "../httpHooks/usePatchHook";
import { updateAuthData } from "../../redux/slices/authSlice";

export default function useHandleDarkMode() {
  const dispatch = useDispatch();
  const PATCH = usePatchHook();

  const changeMode = async (e, command, appAppearance) => {
    e.preventDefault();

    let change = { ...appAppearance, dark_mode: command };

    let { status, success, message } = await PATCH(
      change,
      "/api/user/account/setting/appearance/dark-mode",
      "application/json;charset=UTF-8",
      true
    );

    if (status === 200) {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );

      dispatch(
        updateAuthData({
          appearance: change,
        })
      );
    } else {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );
    }
  };

  return changeMode;
}
