import React, { useState } from "react";
import "./verifyNewChanges.scss";
import ERROR from "../../../../reusableComponents/infomativeComponents/ERROR";
import Ellipsis from "../../../../reusableComponents/loadingComponents/ellipsis";
import Countdown from "../../../../reusableComponents/otherComponents/countdown";
import SixInput from "../../../../reusableComponents/otherComponents/sixInput";
import usePostHook from "../../../../../hooks/httpHooks/usePostHook";
import useGetHook from "../../../../../hooks/httpHooks/useGetHook";
import { setAlert, setVerified } from "../../../../../redux/slices/uiSlice";
import { useDispatch } from "react-redux";
import { updateAuthData } from "../../../../../redux/slices/authSlice";

export default function DoVerification({newEmail}) {
  const dispatch = useDispatch();
  const GET = useGetHook();
  const POST = usePostHook();
  const [item, setItem] = useState({
    error: null,
    isLoaded: { isSubmitted: true, isResent: true },
    isDisabled: { submit: false, resend: false },
    input: "",
    clearInput: false,
    suspend: { waiting: false, time: 0 },
  });

  async function handleSubmit(e) {
    e.preventDefault();
    setItem((rest) => ({
      ...rest,
      isDisabled: { submit: true, resend: true },
      isLoaded: { isSubmitted: false, isResent: true },
      error: null,
    }));
    const response = await POST(
      { code: item.input },
      "/api/user/email/verification/done",
      false
    );

    if (response.status !== 200) {
      setItem((rest) => ({
        ...rest,
        error: response?.inputError?.code || {
          message: response?.message,
          status: response?.status,
        },
        isDisabled: {
          submit: false,
          resend: !item.isDisabled.resend ? false : true,
        },
        isLoaded: { isSubmitted: true, isResent: true },
      }));
    } else {
      setItem((rest) => ({
        ...rest,
        isDisabled: { submit: false, resend: false },
        isLoaded: { isSubmitted: true, isResent: true },
        suspend: { waiting: false, time: 0 },
        input: "",
        clearInput: true,
      }));
      dispatch(setVerified(true));
      dispatch(updateAuthData({ email: newEmail}));
      dispatch(setAlert({ message: response.message, success: true }));
    }
    return;
  }

  async function resendCode() {
    setItem((rest) => ({
      ...rest,
      isLoaded: { isSubmitted: true, isResent: false },
      isDisabled: { submit: true, resend: true },
      error: null,
    }));
    const { message, success, time, status } = await GET(
      "/api/user/email/verificaiton/resend",
      false
    );

    if (success && status === 200) {
      setItem((rest) => ({
        ...rest,
        isLoaded: { isSubmitted: true, isResent: true },
        error: null,

        isDisabled: { submit: false, resend: true },
        suspend: { waiting: true, time },
      }));

      dispatch(setAlert({ message, success: true }));
    } else if (!success && status !== 202) {
      return setItem((rest) => ({
        ...rest,
        isLoaded: { isSubmitted: true, isResent: true },
        isDisabled: { submit: false, resend: false },
        error: {
          message: message,
          status: status,
        },
      }));
    } else {
      setItem((rest) => ({
        ...rest,
        error: {
          message: message,
          status: status,
        },
        suspend: { waiting: false, time: 0 },
      }));
    }
  }

  function get_Otp_Value(value) {
    setItem((rest) => ({
      ...rest,
      input: value,
      error: null,
    }));
  }

  function reset() {
    setItem((rest) => ({
      ...rest,
      isDisabled: { submit: false, resend: false },
      isLoaded: { isSubmitted: true, isResent: true },
      suspend: { waiting: false, time: 0 },
    }));
  }

  function cancel() {
    dispatch(setVerified(true));
    setItem((rest) => ({
      ...rest,
      clearInput: true,
    }));
    return reset();
  }

  return (
    <div className="verify_changes">
      <SixInput
        get_otp={get_Otp_Value}
        clearInput={item.clearInput}
        setItem={setItem}
      />

      <ERROR error={item?.error} align={"center"} />

      {!item.isLoaded.isSubmitted ? (
        <Ellipsis />
      ) : (
        <div className="btnsToVerify">
          <input
            disabled={item.isDisabled.submit}
            className={`verification_btn ${
              item.isDisabled.submit ? `disabled_btn` : ""
            }`}
            id="submitCode"
            type="submit"
            onClick={handleSubmit}
            value="Next"
          />

          {item.suspend.waiting ? (
            <Countdown duration={item.suspend} forceStop={setItem} cb={reset} />
          ) : !item.isLoaded.isResent ? (
            <Ellipsis />
          ) : (
            <input
              disabled={item.isDisabled.resend}
              id="resendCode"
              type="button"
              onClick={resendCode}
              value="Send a new code"
              className={`verification_btn ${
                item.isDisabled.resend ? `disabled_btn` : ""
              }`}
            />
          )}
          <input
            id="cancelVerification"
            type="button"
            value="Cancel"
            onClick={cancel}
          />
        </div>
      )}
    </div>
  );
}
