export default function defaultMode(change) {
  let mode = change || "system_default";

  let system_default = window.matchMedia(
    "(prefers-color-scheme: dark)"
  ).matches;
  let currentHour = new Date().getHours();
  let auto = currentHour >= 18 || currentHour < 6;

  if (mode === "auto") {
    return auto;
  } else if (mode === "system_default") {
    return system_default;
  } else if (mode === "on") {
    return true;
  } else {
    return false;
  }
}
