import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAlert } from "../../redux/slices/uiSlice";
import { deleteNote } from "../../redux/slices/noteSlice";

const useSocketEventHandlers = () => {
  const { socket } = useSelector((state) => state.webSocket);
  const { savedNotes } = useSelector((state) => state.notes);
  const dispatch = useDispatch();

  useEffect(() => {
    function isNoteDeleted(info) {
      if (info.success) {
        dispatch(deleteNote(info?.noteID));
      }
      dispatch(setAlert({ success: info.success, message: info.message }));
      return;
    }

    if (socket) {
      socket.on("note-deleted", isNoteDeleted);
    }

    return () => {
      if (socket) {
        socket.off("note-deleted", isNoteDeleted);
      }
    };
  }, [socket, savedNotes]);
};

export default useSocketEventHandlers;
