import noteEvents from "../../../hooks/socketHooks/noteEvents";
import { useNavigate, useParams } from "react-router-dom";

export default function NoteDropdown({ setColorCode }) {
  const { deleteNoteFromDb } = noteEvents();
  const param = useParams();
  const navigate = useNavigate();

  const colorCode = [
    "#FC6736",
    "#AC35E6",
    "#E6C935",
    "#35E646",
    "#35E3E6",
    "#4A35E6",
    "#87A922",
    "#E6309D",
    "#96EFFF",
    "#003CFA",
    "#FF004D",
    "#86A7FC",
  ];

  const handleDelete = (noteID) => {
    deleteNoteFromDb(noteID);

    navigate(-1);
    return;
  };

  return (
    <div className="nav-dropdown fadeInOut-anim">
      <ul className="nav-dropdown-menu">
        <li className="colorContainer">
          {colorCode.map((e, i) => (
            <button
              key={i}
              style={{ background: `${e}` }}
              onClick={() => setColorCode(e)}
              className="colorCode scaleIt"
            ></button>
          ))}
        </li>
        <li className="hoverIt">
          <button
            onClick={() => handleDelete(param.noteid)}
            type="button"
            className="hoverIt"
          >
            <i className="fa-solid fa-trash"></i>Delete Note
          </button>
        </li>
      </ul>
    </div>
  );
}
