import { useSelector } from "react-redux";
import { appearance } from "../../../../redux/slices/authSlice";
import useHandleDarkMode from "../../../../hooks/componentHooks/handleDarkModeHook"; 
import useToggleHook from "../../../../hooks/componentHooks/handleToggleHook";
 

export default function Appearance() {
  const appAppearance = useSelector(appearance);
  const handleDarkMode = useHandleDarkMode();
  const handleGridView = useToggleHook({
    currentState:appAppearance,
    stateToChange:["appearance","grid_view"],
    url:"/api/user/account/setting/appearance/grid-view"
  })

  let themes = [
    ["Auto", "auto"],
    ["System default", "system_default"],
    ["On", "on"],
    ["Off", "off"],
  ];

  return (
    <div className="appearance s_item">
      <div className="section_name">
        <span>Appearance</span>
      </div>
      <div className="modify">
        <div id="darkMode">
          <div className="small_header">
            <span>Dark Mode</span>
          </div>

          <div id="dark_mode_category">
            {themes.map((e, i) => (
              <div key={i} className="dark_mode_options hoverIt">
                <label
                  className={
                    appAppearance.dark_mode === e[1] ? "selected_options" : ""
                  }
                  htmlFor={e[1]}
                >
                  {e[0]}
                </label>
                <input
                  id={e[1]}
                  value={e[1]}
                  checked={appAppearance.dark_mode === e[1]}
                  onChange={(event) =>
                    handleDarkMode(event, e[1], appAppearance)
                  }
                  type="radio"
                  name="dark_mode"
                />
              </div>
            ))}
          </div>
        </div>
        <div className="wide-list-btn feature_available hoverIt">
          <label className="feature_available" htmlFor="notesView">
            Grid View
          </label>
          <input
            id="notesView"
            className="toggle_switch"
            checked={appAppearance.grid_view}
            onChange={handleGridView}//(e) => handleGirdMode(e, appAppearance)}
            type="checkbox"
          />
        </div>
      </div>
    </div>
  );
}
