import { createAsyncThunk } from "@reduxjs/toolkit";
import io from "socket.io-client";

export const connectToSocket = createAsyncThunk(
  "socket/connect",
  async (socketToken) => {
    try {
      const socket = configSocket(socketToken);
      return socket;
    } catch (error) {
      return error;
    }
  }
);

export const disconnectFromSocket = createAsyncThunk(
  "socket/disconnect",
  async (args, thunkAPI) => {
    const { socket } = thunkAPI.getState().webSocket;

    if (socket) {
      socket.disconnect();
      return "Socket disconnected";
    } else {
      throw new Error("Socket instance not found");
    }
  }
);

function configSocket(socketToken) {
  const newSocket = io("https://notebuzz.vercel.app", {
    query: {
      token: socketToken, // For socket verification
    },
  });

  newSocket.emit("user-online");

  return newSocket;
}
