import { useSelector } from "react-redux";

export default function useSaveNotes() {
  const { socket } = useSelector((state) => state.webSocket);

  function makeNote(note) {
    if (socket) {
      socket.emit("make-a-note", note);
    }
  }

  function deleteNoteFromDb(noteID) {
    if (socket) {
      socket.emit("delete-a-note", noteID);
    }
  }

  return { makeNote, deleteNoteFromDb };
}
