import { useEffect, useState } from "react";
import useGetHook from "../httpHooks/useGetHook";
import { useDispatch } from "react-redux";
import { setNoteSavingStatus } from "../../redux/slices/noteSlice";
import { setAlert } from "../../redux/slices/uiSlice";

export default function GetCurrentNote(noteID, setInput) {
  const [newNote, setNewNote] = useState(true);
  const dispatch = useDispatch();
  const data = useGetHook(`/api/user/note/${noteID}`, true);

  useEffect(() => {
    dispatch(setNoteSavingStatus("saving"));
    if (data && data.status === 200) {
      const { noteTitle, noteContent, colorCode } = data.note;
      setInput((inputs) => ({
        ...inputs,
        noteTitle,
        noteContent,
        colorCode,
      }));

      setNewNote(false);
      dispatch(setNoteSavingStatus("saved"));
    } else if (data && data?.status !== 404) {
      dispatch(setNoteSavingStatus("failed"));
      dispatch(setAlert({ message: data?.message, success: false }));
    }else if (data && data?.status === 404){
       dispatch(setNoteSavingStatus("saved"));
    }

    return () => dispatch(setNoteSavingStatus("idle"));
  }, [data]);

  return newNote;
}
