import { useEffect, useState } from "react";
import { Outlet } from "react-router-dom";
import useRefresh from "../../../hooks/usefulHooks/useRefresh";
import { useSelector } from "react-redux";
import Spinner from "../../reusableComponents/loadingComponents/spinner";

const PersistLogin = () => {
  const { authData } = useSelector((state) => state.userAuth);
  const [isAppLoading, setIsAppLoading] = useState(true);
  const refresh = useRefresh();

  useEffect(() => {
      let delay;

    const verifyToken = async () => {
      try {
        await refresh();
      } catch (error) {
        console.error(error);
      } finally { 
         delay = setTimeout(() => setIsAppLoading(false), 1000);
      }
    };

    !authData?.accessToken ? verifyToken() : setIsAppLoading(false);
    return () => {
      clearTimeout(delay);
    };
  }, []);

  return (
    <>
      {isAppLoading ? (
        <div className="smooth-anim" id="AppLoading">
          <img
            className="smooth-anim"
            src="https://die739ygwougn.cloudfront.net/assets/logo192.png"
            alt="App_logo"
          />
          <h1>NoteBuzz</h1>
          <Spinner />
        </div>
      ) : (
        <Outlet />
      )}
    </>
  );
};

export default PersistLogin;
