import "./settings.scss";
import Header from "../../reusableComponents/header";
import { setShowSettings } from "../../../redux/slices/uiSlice";
import Appearance from "./elements/appearance";
import Security from "./elements/security";
import About from "./elements/about";
import Feedback from "./elements/feedback";

export default function Settings() {
  return (
    <div id="settings">
      <div className="window fadeIn background_component">
        <Header name={"Settings"} func={setShowSettings} />
        <Appearance />
        <Security />
        <About />
        <Feedback />
      </div>
    </div>
  );
}
