import { createSlice } from "@reduxjs/toolkit";

const initialState = { isAuthenticated: false, authData: null };

const authSlice = createSlice({
  name: "UserAuth",
  initialState,
  reducers: {
    setAuth: (state, action) => {
      state.isAuthenticated = true;
      state.authData = action.payload;
    },
    updateAuthData: (state, action) => {
      if (state.isAuthenticated) {
        let [key, value] = [
          Object.keys(action.payload),
          Object.values(action.payload),
        ];
        state.authData.user[key[0]] = value[0];
      }
    },
    removeAuth: (state) => {
      state.isAuthenticated = false;
      state.authData = null;
    },
  },
});

const getUserData = (state) => state.userAuth.authData?.user;
const getAuthData = (state) => state.userAuth.authData;
const appearance = (state) => state.userAuth.authData?.user?.appearance;
const security = (state) => state.userAuth.authData?.user?.security;

export { getUserData, getAuthData, appearance, security };
export const { setAuth, updateAuthData, removeAuth } = authSlice.actions;
export default authSlice.reducer;
