import "./note.scss";
import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setNoteDropdown } from "../../../redux/slices/uiSlice";
import { insertNewNote, editNote } from "../../../redux/slices/noteSlice";
import Refresh from "../../reusableComponents/loadingComponents/refresh";
import NoteDropdown from "./noteDropdown";
import noteEvents from "../../../hooks/socketHooks/noteEvents";
import useDebounceHook from "../../../hooks/utilityHooks/debounceHook";
import GetCurrentNote from "../../../hooks/componentHooks/getCurrentNote";

export default function Note() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const param = useParams();
  const { makeNote, deleteNoteFromDb } = noteEvents();
  const { showNoteDropdown } = useSelector((state) => state.userInterface);
  const { noteSavingStatus } = useSelector((state) => state.notes);
  const [modified, setModified] = useState(false);
  const [input, setInput] = useState({
    noteTitle: "",
    noteContent: "",
    colorCode: "#E65F35",
    timestamp: new Date(),
    noteID: param.noteid,
  });
  const isNewNote = GetCurrentNote(param.noteid, setInput);
  useDebounceHook(saveThisNote, modified, input);

  function saveThisNote() {
    let title = input.noteTitle
      ? { noteTitle: input.noteTitle.trim() }
      : { noteTitle: "Untitled" };
    if (!isNewNote) {
      dispatch(editNote({ ...input, ...title }));
    } else {
      if (input.noteTitle || input.noteContent) {
        dispatch(insertNewNote({ ...input, ...title }));
      }
    }

    if (
      !input.noteTitle &&
      !input.noteContent &&
      modified
      // if user clear note and noteTitle so delete note
    ) {
      deleteNoteFromDb(input.noteID);
    }
    makeNote(input);
    return;
  }

  const back = () => {
    saveThisNote();
    navigate(-1);
    return;
  };

  function handleInput(e) {
    let element = e.target;

    setInput((other) => ({
      ...other,
      [element.name]: element.value,
      timestamp: new Date(),
    }));
    setModified(true);
  }

  function handleDropdown(e) {
    e.stopPropagation();
    dispatch(setNoteDropdown(!showNoteDropdown));
  }

  function setColorCode(color) {
    setModified(true);
    setInput((other) => ({
      ...other,
      colorCode: color,
    }));
  }

  let Title = input.noteTitle === "Untitled" ? "" : input.noteTitle;
  let isSaving = {
    idle: [
      <i
        title="No update to save!"
        className="fa-solid fa-circle-exclamation"
      ></i>,
      "Type your thoughts here; they will be saved automatically.",
    ],
    saving: [<Refresh />, "Loading..."],
    saved: [
      <i title="Note saved!" className="fa-solid fa-circle-check"></i>,
      "Type your thoughts here; they will be saved automatically.",
    ],
    failed: [
      <i className="fa-solid fa-xmark"></i>,
      "Something went wrong please reopen this note",
    ],
  };

  return (
    <div id="note" className="fadeIn">
      <div className="note_header">
        <div id="note_name">
          <button onClick={back} className="back_btn hoverIt">
            <i className="fa fa-arrow-left"></i>
          </button>
          <input
            style={{ borderColor: input.colorCode, color: input.colorCode }}
            type="text"
            name="noteTitle"
            id="note_title_box"
            placeholder="Enter note title..."
            value={Title}
            onChange={handleInput}
          />
        </div>
        <div className="current-note-options">
          <div className="current-note-status">
            {isSaving[noteSavingStatus][0]}
          </div>
          <button className="option_btn hoverIt" onClick={handleDropdown}>
            <i className="fa-solid fa-ellipsis-vertical"></i>
          </button>
        </div>
      </div>
      <div id="note_editor">
        <textarea
          style={{ borderColor: input.colorCode }}
          name="noteContent"
          id="note_content_box"
          placeholder={isSaving[noteSavingStatus][1]}
          value={input.noteContent}
          onChange={handleInput}
        ></textarea>
      </div>
      {showNoteDropdown ? <NoteDropdown setColorCode={setColorCode} /> : ""}
    </div>
  );
}
