import { useState } from "react";
import ERROR from "../../../reusableComponents/infomativeComponents/ERROR";
import Ellipsis from "../../../reusableComponents/loadingComponents/ellipsis";
import useUpdate from "../../../../hooks/utilityHooks/fieldUpdateHook";
import useFieldChecker from "../../../../hooks/utilityHooks/fieldCheckerHook";
import usePatchHook from "../../../../hooks/httpHooks/usePatchHook";
import { useDispatch, useSelector } from "react-redux";
import {
  getUserData,
  updateAuthData,
} from "../../../../redux/slices/authSlice";
import { setAlert, setVerified } from "../../../../redux/slices/uiSlice";
import DoVerification from "./updateVerification/verifyNewChanges";

export default function PersonalInfo() {
  const { isVerified } = useSelector((state) => state.userInterface);
  const UPDATED_FIELDS = useUpdate();
  const CHECK_FIELD = useFieldChecker();
  const dispatch = useDispatch();
  const PATCH = usePatchHook();
  const user = useSelector(getUserData);
  const [item, setItem] = useState({
    inputError: null,
    isLoaded: true,
  });

  const [input, setInput] = useState({
    email: user.email,
    username: user.username,
  });

  function handleChange(e) {
    const { name, value } = e.target;
    let field = { [name]: value };
    setInput((rest) => ({ ...rest, ...field }));
    let requireFields = CHECK_FIELD(UPDATED_FIELDS(field, user));
    setItem((rest) => ({ ...rest, inputError: requireFields }));
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setItem((rest) => ({ ...rest, isLoaded: false, inputError: null }));

    const response = await PATCH(
      UPDATED_FIELDS(input, user), // only send updated fields to server
      "/api/user/account-information",
      "application/json;charset=UTF-8",
      false
    );
    if (response.status === 200) {
      setItem((rest) => ({ ...rest, isLoaded: true }));
      dispatch(setAlert({ success: true, message: response?.message }));
      dispatch(updateAuthData({ username: input.username }));
    } else if (response.status === 202) {
      setItem((rest) => ({ ...rest, isLoaded: true }));
      dispatch(setVerified(false));
    } else if (response.status === 400) {
      setItem((rest) => ({
        ...rest,
        isLoaded: true,
        inputError: response.inputError,
      }));
    } else {
      setItem((rest) => ({ ...rest, isLoaded: true }));
      dispatch(setAlert({ success: false, message: response.message }));
    }
  }

  return (
    <form onSubmit={handleSubmit} className="ac_form sec">
      <div className="editable_info">
        <div className="info_sec">
          <span>Account Information</span>
        </div>
        <div className="input_wrapper">
          <label htmlFor="username">
            <input
              type="text"
              id="username"
              name="username"
              className={item.inputError?.username ? "input_error" : ""}
              value={input.username}
              onChange={handleChange}
              placeholder="Username"
              maxLength="30"
              
            />
            <span className="spanPlaceholder">Username</span>
          </label>
          <ERROR error={item.inputError?.username} />
        </div>
        <div className="input_wrapper">
          <label htmlFor="email">
            <input
              type="email"
              id="email"
              name="email"
              className={item.inputError?.email ? "input_error" : ""}
              value={input.email}
              onChange={handleChange}
              placeholder="Email"
              
            />
            <span className="spanPlaceholder">Email</span>
          </label>
          <ERROR error={item.inputError?.email} />
        </div>
        {UPDATED_FIELDS(input, user).hasOwnProperty("email") ? (
          <p className="info">
            A verification code will be sent to this email address.
          </p>
        ) : (
          ""
        )}
        {!isVerified ? <DoVerification newEmail={input.email} /> : ""}
        <div className="verifyEmail"></div>
        {Object.keys(UPDATED_FIELDS(input, user)).length && isVerified ? (
          <div className="profile_save_button">
            {!item.isLoaded ? (
              <Ellipsis />
            ) : (
              <input
                type="submit"
                value="Save changes"
                className="myBtn pressIt"
              />
            )}
          </div>
        ) : (
          ""
        )}
      </div>
    </form>
  );
}
