import { useDispatch, useSelector } from "react-redux";
import { initilizeSavedNotes } from "../../redux/slices/noteSlice";
import { setAlert, setAppLoading } from "../../redux/slices/uiSlice";
import { useEffect } from "react";
import useGetHook from "../httpHooks/useGetHook";

export default function useGetNotes() {
  const dispatch = useDispatch();
  const { savedNotes } = useSelector((state) => state.notes);
  const response = useGetHook("/api/user/notes", !savedNotes.length, true);

  useEffect(() => {
    if (response && response.status === 200) {
      dispatch(initilizeSavedNotes(response.notes));
    } else if (response) {
      dispatch(setAlert({ message: response.message, success: false }));
    }

    return () => dispatch(setAppLoading(false));
  }, [response]);

  return savedNotes;
}
