import "./navbar.scss";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setDropdown } from "../../../../redux/slices/uiSlice";
import { getUserData } from "../../../../redux/slices/authSlice";
import { nanoid } from "@reduxjs/toolkit";

export default function Navbar() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { dropdown } = useSelector((state) => state.userInterface);
  const { avatar } = useSelector(getUserData);
  const handleDropdown = (e) => {
    e.stopPropagation();
    dispatch(setDropdown(!dropdown));
  };

  return (
    <nav id="navbar">
      <div id="app_name_and_logo">
        <h1>NoteBuzz</h1>
      </div>
      <div id="nav_options">
        <button
          onClick={() => navigate(`/note/${nanoid()}`)}
          className="option_btn hoverIt pressIt"
          title="Take a note"
        >
          <i className="fa-solid fa-plus"></i>
        </button>
        <input
          type="image"
          src={avatar}
          onClick={handleDropdown}
          className="option_btn hoverIt pressIt"
          alt="user_avatar"
          title="Options"
        />
      </div>
    </nav>
  );
}
