import { useState } from "react";
import usePatchHook from "../../../../hooks/httpHooks/usePatchHook";
import { useDispatch, useSelector } from "react-redux";
import { setAppLoadingAndAlert } from "../../../../redux/slices/uiSlice";
import {
  getUserData,
  updateAuthData,
} from "../../../../redux/slices/authSlice";

export default function UserAvatar() {
  const PATCH = usePatchHook();
  const dispatch = useDispatch();
  const { avatar } = useSelector(getUserData);
  const [currentAvatar, setCurrentAvatar] = useState(avatar);
  const [displayAvatars, setDisplayAvatar] = useState(false);
  const avatars = [
    "https://die739ygwougn.cloudfront.net/avatars/girl.png",
    "https://die739ygwougn.cloudfront.net/avatars/woman1.png",
    "https://die739ygwougn.cloudfront.net/avatars/woman.png",
    "https://die739ygwougn.cloudfront.net/avatars/woman2.png",
    "https://die739ygwougn.cloudfront.net/avatars/boy.png",
    "https://die739ygwougn.cloudfront.net/avatars/boy1.png",
    "https://die739ygwougn.cloudfront.net/avatars/boy3.png",
    "https://die739ygwougn.cloudfront.net/avatars/boy4.png",
    "https://die739ygwougn.cloudfront.net/avatars/secret-agent.png",
    "https://die739ygwougn.cloudfront.net/avatars/man1.png",
    "https://die739ygwougn.cloudfront.net/avatars/elephant.png",
    "https://die739ygwougn.cloudfront.net/avatars/giraffe.png",
    "https://die739ygwougn.cloudfront.net/avatars/dog.png",
    "https://die739ygwougn.cloudfront.net/avatars/panda.png",
    "https://die739ygwougn.cloudfront.net/avatars/goat.png",
    "https://die739ygwougn.cloudfront.net/avatars/ninja.png",
    "https://die739ygwougn.cloudfront.net/avatars/astronaut.png",
    "https://die739ygwougn.cloudfront.net/avatars/batman.png",
    "https://die739ygwougn.cloudfront.net/avatars/superhero.png",
    "https://die739ygwougn.cloudfront.net/avatars/spiderman.png",
  ];

  async function handleAvatar(selectedAvatar) {
    if (selectedAvatar !== currentAvatar) {
      const change = { avatar: selectedAvatar };
      let { status, success, message } = await PATCH(
        change,
        "/api/user/account/avatar",
        "application/json;charset=UTF-8",
        true
      );

      if (status === 200) {
        dispatch(
          setAppLoadingAndAlert({
            isLoading: false,
            alert: { success, message },
          })
        );

        dispatch(updateAuthData(change));
        setCurrentAvatar(selectedAvatar);
      } else {
        dispatch(
          setAppLoadingAndAlert({
            isLoading: false,
            alert: { success, message },
          })
        );
      }
    }
  }

  let showMore = displayAvatars ? "up" : "down";

  return (
    <div className="userAvatars sec">
      <img
        crossOrigin="anonymous"
        className="current-avatar"
        src={currentAvatar}
        alt="userAvatar"
      />

      <div className="more_avatar">
        <button
          className="showMoreBtn hoverIt"
          type="button"
          onClick={() => setDisplayAvatar(!displayAvatars)}
        >
          <span>Change Avatar</span>

          <i className={`fas fa-angle-${showMore}`}></i>
        </button>
        {displayAvatars ? (
          <div className="avatars-category fadeIn">
            {avatars.map((e, i) => (
              <input 
                type="image"
                onClick={() => handleAvatar(e)}
                key={i}
                className={
                  e === currentAvatar
                    ? "avatars selectedAvatar scaleIt"
                    : "avatars scaleIt"
                }
                src={e}
                alt="availableAvatar"
              />
            ))}
          </div>
        ) : (
          " "
        )}
      </div>
    </div>
  );
}
