import { useEffect, useState } from "react";
import { setAppLoading } from "../../redux/slices/uiSlice";
import { useDispatch } from "react-redux";
import handleResponse from "../../modules/responseHandler";
import useAxiosPrivate from "../usefulHooks/useAxiosPrivate";

export default function useGetHook(
  url,
  requestNow = false,
  showProgress = false
) {
  const [response, setResponse] = useState(null);
  const axiosPrivate = useAxiosPrivate();
  const dispatch = useDispatch();

  useEffect(() => {
    const abortController = new AbortController();

    const makeRequest = async () => {
      try {
        dispatch(setAppLoading(showProgress));
        const data = await axiosPrivate.get(url, {
          signal: abortController.signal,
        });
        setResponse(handleResponse(data, true));
      } catch (error) {
        let err = handleResponse(error, false);
        if (err.message !== "canceled") {
          setResponse(err);
        }
      }  
    };  

    if (requestNow) {
      makeRequest();
    }

    return () => {
      abortController.abort(); // Cancel the request when the component unmounts
    };
  }, [url]);

  return response;
}
