import "./App.scss";
import Layout from "./layout";
import { Route, Routes } from "react-router-dom";
import Home from "../components/protectedComponents/home/home";
import Signin from "./authComponents/authentication/signin";
import Signup from "./authComponents/authentication/signup";
import Note from "./protectedComponents/NoteEditor/note";
import ManageAccount from "./protectedComponents/account/manageAccount";
import Settings from "./protectedComponents/settings/settings";
import RequireAuth from "./authComponents/outlets/requireAuth";
import PersistLogin from "./authComponents/outlets/persisitSignin";
import Error from "./Error";
import { useDispatch, useSelector } from "react-redux";
import { setAppFocus } from "../redux/slices/uiSlice";
import { setNoteDropdown } from "../redux/slices/uiSlice";
import AccountRecovery from "./authComponents/outlets/accountRecovery";
import SetPassword from "./protectedComponents/SetPassword";
import Recover from "./authComponents/recovery/recover";
import { appearance } from "../redux/slices/authSlice";
import defaultTheme from "../modules/defaultTheme";

function App() {
  const appAppearance = useSelector(appearance);
  const dark_mode = defaultTheme(appAppearance?.dark_mode);
  const dispatch = useDispatch();
  let isDark = dark_mode ? "dark" : "light";

  const handleAppFocus = (e) => {
    e.stopPropagation();
    dispatch(setAppFocus(true));
    dispatch(setNoteDropdown(false));
  };

  return (
    <div onClick={handleAppFocus} className={`App ${isDark}`}>
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route path="signin" element={<Signin />} />
          <Route path="signup" element={<Signup />} />

          <Route path="password/recovery" element={<Recover />} />
          <Route element={<AccountRecovery />}>
            <Route
              path="user/passwordrecovery/setnewpassword/:token"
              element={<SetPassword />}
            />
          </Route>

          <Route element={<PersistLogin />}>
            <Route element={<RequireAuth />}>
              <Route path="/" element={<Home />} />
              <Route path="/note/:noteid" element={<Note />} />
              <Route path="/manage-account" element={<ManageAccount />} />
              <Route path="/settings" element={<Settings />} />
            </Route>
          </Route>
          <Route path="*" element={<Error />} />
        </Route>
      </Routes>
    </div>
  );
}

export default App;
