import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getUserData, security } from "../../../../redux/slices/authSlice";
import useToggleHook from "../../../../hooks/componentHooks/handleToggleHook";
import ERROR from "../../../reusableComponents/infomativeComponents/ERROR";
import usePatchHook from "../../../../hooks/httpHooks/usePatchHook"; 
import useUpdate from "../../../../hooks/utilityHooks/fieldUpdateHook";
import {
  setAppLoading,
  setAppLoadingAndAlert,
} from "../../../../redux/slices/uiSlice";

export default function Security() {
  const PATCH = usePatchHook();
  const user = useSelector(getUserData);
  const { isLoading } = useSelector((state) => state.userInterface);
  const dispatch = useDispatch();
  const [input, setInput] = useState({
    current_password: "",
    new_password: "",
    confirm_password: "",
  });
  const [item, setItem] = useState({
    inputError: null,
    isLoaded: true,
    passwordVisibility: false,
  });
 
  const UPDATED_FIELDS = useUpdate();

  const accountSecurity = useSelector(security);
  const handleTwoStepVerification = useToggleHook({
    currentState: accountSecurity,
    stateToChange: ["security", "two_step_verification"],
    url: "/api/user/account/setting/security/two-step-verification",
  });
  const handleLoginNotification = useToggleHook({
    currentState: accountSecurity,
    stateToChange: ["security", "login_notification"],
    url: "/api/user/account/setting/security/login-notification",
  });

  async function handleSubmit(e) {
    e.preventDefault();

    const response = await PATCH(
      input,
      "/api/user/account/setting/security/password",
      "application/json;charset=UTF-8",
      true
    );
    if (response.status === 200) {
      const { success, message } = response;
      setInput((rest) => ({
        ...rest,
        current_password: "",
        new_password: "",
        confirm_password: "",
      }));
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );
      setItem((rest) => ({ ...rest, inputError: null }));
    } else if (
      response.status === 400 &&
      response.hasOwnProperty("inputError")
    ) {
      setItem((rest) => ({ ...rest, inputError: response?.inputError }));
      dispatch(setAppLoading(false));
    } else {
      dispatch(
        setAppLoadingAndAlert({
          alert: { success: false, message: response?.message },
          isLoading: false,
        })
      );
    }
    return;
  }

  function handleChange(e) {
    const { name, value } = e.target;
    let field = { [name]: value };
    setInput((rest) => ({ ...rest, ...field }));
    setItem((rest) => ({ ...rest, inputError: null }));
    UPDATED_FIELDS(field, user);
  }

  function trackChanges(changes) {
    const changed = {};
    for (let key in changes) {
      if (changes[key] !== "") {
        changed[key] = changes[key];
      }
    }
    return changed;
  }

  const handlePasswordVisibility = () =>
    setItem((rest) => ({
      ...rest,
      passwordVisibility: !item.passwordVisibility,
    }));

  const showPassword = item.passwordVisibility
    ? ["ON", "text"]
    : ["OFF", "password"];

  return (
    <div className="Security s_item">
      <form>
        <div className="section_name">
          <span>Security</span>
        </div>
        <div className="wide-list-btn feature_available hoverIt">
          <label className="feature_available" htmlFor="Login_notification">
            Login notification
          </label>
          <input
            id="Login_notification"
            className="toggle_switch"
            checked={accountSecurity.login_notification}
            onChange={(e) => handleLoginNotification(e, accountSecurity)}
            type="checkbox"
          />
        </div>
        <div className="wide-list-btn feature_available hoverIt">
          <label className="feature_available" htmlFor="Two_step_verification">
            Two-step-verification
          </label>
          <input
            id="Two_step_verification"
            className="toggle_switch"
            checked={accountSecurity.two_step_verification}
            onChange={handleTwoStepVerification}
            type="checkbox"
          />
        </div>
      </form>
      <form
        onSubmit={handleSubmit}
        id="submit_password"
        className="security_form"
      >
        <div className="security_input_div">
          <div className="input_wrapper">
            <label htmlFor="current_password">
              <input
                type={showPassword[1]}
                id="current_password"
                name="current_password"
                value={input.current_password}
                onChange={handleChange}
                aria-label="Username"
                aria-describedby="addon-wrapping"
                placeholder="Current Password"
              />
              <span className="spanPlaceholder">Current Password</span>
            </label>
            <ERROR error={item.inputError?.current_password} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="new_password">
              <input
                id="new_password"
                type={showPassword[1]}
                name="new_password"
                aria-label="Username"
                value={input.new_password}
                onChange={handleChange}
                aria-describedby="addon-wrapping"
                placeholder="New Password"
              />
              <span className="spanPlaceholder">New Password</span>
            </label>
            <ERROR error={item.inputError?.new_password} />
          </div>

          <div className="input_wrapper">
            <label htmlFor="confirm_password">
              <input
                id="confirm_password"
                type={showPassword[1]}
                name="confirm_password"
                aria-label="Username"
                value={input.confirm_password}
                onChange={handleChange}
                aria-describedby="addon-wrapping"
                placeholder="Confirm Password"
              />
              <span className="spanPlaceholder">Confirm Password</span>
            </label>
            <ERROR error={item.inputError?.confirm_password} />
          </div>
          <div id="show_password">
            <button
              type="button"
              onClick={handlePasswordVisibility}
              className="hoverIt"
              id="show_pwd_btn"
            >
              <span>Password visibility</span> : <span>{showPassword[0]}</span>
            </button>
          </div>
        </div>
        <div className="submit_btn_div">
          {Object.keys(trackChanges(input)).length !== 0 && !isLoading ? (
            <input type="submit" value="Set New Password" className="myBtn" />
          ) : (
            ""
          )}
        </div>
      </form>
    </div>
  );
}
