import "./home.scss";
import Navbar from "./elements/navbar";
import NavDropdown from "./elements/dropdown";
import { useSelector } from "react-redux";
import Settings from "../settings/settings";
import ManageAccount from "../account/manageAccount";
import SavedNotes from "./elements/savedNotes";

export default function Home() {
  const { dropdown, showSettings, showAccountSettings } = useSelector(
    (state) => state.userInterface
  );

  return (
    <div id="home" className="fadeIn">
      <Navbar />
      <SavedNotes />
      {dropdown ? <NavDropdown /> : ""}
      {showSettings ? <Settings /> : ""}
      {showAccountSettings ? <ManageAccount /> : ""}
    </div>
  );
}
