import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  savedNotes: [],
  noteSavingStatus: "idle",
};

const noteSlice = createSlice({
  name: "Notes",
  initialState,
  reducers: {
    initilizeSavedNotes: (state, action) => {
      // get saved notes from api
      state.savedNotes.push(...action.payload);
    },
    insertNewNote: (state, action) => {
      state.savedNotes.unshift(action.payload);
    },
    editNote: (state, action) => {
      let allNotes = state.savedNotes;
      let selectedNote = action.payload;

      for (let i = 0, len = allNotes.length; i < len; i++) {
        if (allNotes[i]?.noteID === selectedNote?.noteID) {
          allNotes.splice(i, 1, selectedNote);
          break;
        }
      }
    },
    deleteNote: (state, action) => {
      let allNotes = state.savedNotes;
      let selectedNoteID = action.payload;

      for (let i = 0, len = allNotes.length; i < len; i++) {
        if (allNotes[i]?.noteID === selectedNoteID) {
          allNotes.splice(i, 1);
          break;
        }
      }
    },
    setNoteSavingStatus: (state, action) => {
      state.noteSavingStatus = action.payload;
    },
  },
});

export const {
  initilizeSavedNotes,
  insertNewNote,
  setNoteSavingStatus,
  editNote,
  deleteNote,
} = noteSlice.actions;
export default noteSlice.reducer;
