import "./header.scss";
import { useDispatch } from "react-redux";

export default function Header({ name, func }) {
  const dispatch = useDispatch();

  return (
    <div className="header">
      <div className="header_items">
        <button
          onClick={() => dispatch(func(false))}
          className="back_btn hoverIt"
        >
          <i className="fa fa-arrow-left"></i>
        </button>
        <h2>{name}</h2>
      </div>
    </div>
  );
}
