import { useState } from "react";
import usePostHook from "../../../../hooks/httpHooks/usePostHook";
import { useDispatch, useSelector } from "react-redux";
import { setAppLoadingAndAlert } from "../../../../redux/slices/uiSlice";

export default function Feedback() {
  const [text, setText] = useState("");
  const POST = usePostHook();
  const dispatch = useDispatch();
  const { isLoading } = useSelector((state) => state.userInterface);

  function handleText(e) {
    setText(e.target.value);
  }

  async function handleSubmit(e) {
    e.preventDefault();
    const { success, message, status } = await POST(
      { message: text },
      "/api/user/feedback",
      true
    );
    if (status === 200) {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );
    } else {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );
    }
  }

  let isSending = isLoading ? ["Sending", true] : ["Send", false];

  return (
    <div className="Feedback s_item">
      <div className="section_name">
        <span>Feedback</span>
      </div>
      <form onSubmit={handleSubmit} id="help_form">
        <textarea
          onChange={handleText}
          value={text}
          name="help"
          id="helpText"
          placeholder="Type your message here..."
          cols="30"
          rows="10"
          required
        ></textarea>

        <input
          disabled={isSending[1]}
          className="myBtn"
          type="submit"
          value={isSending[0]}
        />
      </form>
    </div>
  );
}
