import { useDispatch } from "react-redux";
import { setAppLoadingAndAlert } from "../../redux/slices/uiSlice";
import usePatchHook from "../httpHooks/usePatchHook";
import { updateAuthData } from "../../redux/slices/authSlice";

export default function useToggleHook({ currentState, stateToChange, url }) {
  const dispatch = useDispatch();
  const PATCH = usePatchHook();

  const toggle = async (e) => { 
    e.preventDefault();

    let change = {
      ...currentState,
      [stateToChange[1]]: !currentState[stateToChange[1]],
    };

    let { status, success, message } = await PATCH(
      change,
      url,
      "application/json;charset=UTF-8",
      true
    );

    if (status === 200) {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );

      dispatch(
        updateAuthData({
          [stateToChange[0]]: change,
        })
      );
    } else {
      dispatch(
        setAppLoadingAndAlert({
          isLoading: false,
          alert: { success, message },
        })
      );
    }
    return;
  };

  return toggle;
}
